import React from 'react';
import { Popup } from 'react-map-gl';

const LinePopup = ({selectedPoint, setSelectedPoint}) => {
    const {coordinates, gid} = selectedPoint;

    return ( 
        <Popup 
        latitude={coordinates[1]}
        longitude={coordinates[0]}
        offsetTop={-12}
        closeOnClick={false}
        sortByDepth={true}
        onClose={(e) => {
            setSelectedPoint(null);
        }}>
            <table className="edit">
                <thead>
                    <tr>
                        <td>Key</td>
                        <td>Value</td>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Group</td>
                        <td>{gid}</td>
                    </tr>
                    <tr>
                        <td>Entity</td>
                        <td>LineString</td>
                    </tr>
                    <tr>
                        <td>Latitude</td>
                        <td style={{color: "blue"}}>{coordinates[1]}</td>
                    </tr>
                    <tr>
                        <td>Longitude</td>
                        <td style={{color: "blue"}}>{coordinates[0]}</td>
                    </tr>
                </tbody>
            </table>
        </Popup>
     );
}
 
export default LinePopup;